html {
  box-sizing: border-box;
  font-size: 62.5%;
  scroll-behavior: smooth;
}

@media (max-width: 768px) {
  html {
    font-size: 60% !important;
  }
}
*, *:before, *:after {
  box-sizing: inherit;
}

p {
  font-size: 2rem;
}

*::selection {
  background-color: #4b4b4b;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

h1, h2, h3 {
  margin: 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

body {
  font-size: 1.6rem;
  font-family: "Poppins", sans-serif;
}
body.fijar-body {
  overflow: hidden;
}

.content {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
}

img {
  max-width: 100%;
}

body {
  background-color: #1d1d1d;
  color: #f5f5f5;
}

.header__title {
  font-family: "Pirata One", cursive;
  letter-spacing: 1rem;
  font-size: 6rem;
}

@media (min-width: 768px) {
  .main {
    height: 100vh;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2.5rem;
  }
}
.main .bg-img {
  background-image: url(../img/bg.jpg);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center right;
  background-size: cover;
  overflow: hidden;
  height: 30rem;
}
@media (min-width: 768px) {
  .main .bg-img {
    height: auto;
  }
}

.app {
  width: 95%;
  margin: 0 auto;
  padding-top: 2.5rem;
  text-align: center;
  text-transform: uppercase;
  color: #B7B7B7;
  text-shadow: 6px 6px 15px #0c0c0c, -6px -6px 15px #2e2e2e;
}
.app .seccion {
  margin-top: 5rem;
  display: none;
  overflow: hidden;
}
.app .seccion__title {
  font-family: "Pirata One", cursive;
  letter-spacing: 0.5rem;
  font-size: 2.5rem;
}
.app .seccion__text {
  text-transform: none;
  text-align: center;
}

.header {
  margin-bottom: 4rem;
}

.tabs {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
}
.tabs button {
  border: none;
  outline: none;
  color: #B7B7B7;
  background: linear-gradient(145deg, #1f1f1f, #1a1a1a);
  box-shadow: 6px 6px 25px #0c0c0c, -6px -6px 25px #2e2e2e;
  padding: 1.5rem;
  text-transform: uppercase;
  font-weight: 600;
}
.tabs button.actual {
  position: relative;
  border: none;
  outline: none;
  color: rgba(245, 245, 245, 0.3647058824);
  text-shadow: 6px 6px 25px #0c0c0c, -6px -6px 25px #2e2e2e;
  background: #1d1d1d;
  box-shadow: inset 6px 6px 25px #0c0c0c, inset -6px -6px 25px #2e2e2e;
}
.tabs button.actual::before {
  content: "";
  width: 5px;
  height: 5px;
  border-radius: 50%;
  background: #00ff2a;
  position: absolute;
  top: 10px;
  right: 10px;
}

.show-seccion {
  display: block !important;
}

.listado-servicios {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 5rem;
  gap: 4rem;
  padding: 4rem;
}
@media (min-width: 768px) {
  .listado-servicios {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    gap: 4rem;
  }
}
.listado-servicios .nombreServicio {
  font-size: 1.6rem;
  font-weight: 400;
  margin: 0 0 1.5rem 0;
}
.listado-servicios .precioServicio {
  font-family: "Pirata One", cursive, "Poppins", sans-serif;
  letter-spacing: 0.5rem;
  font-weight: bold;
  margin: 0;
}
.listado-servicios .contenedorServicio {
  padding: 3rem 0;
  position: relative;
  outline: none;
  border: none;
  font-size: 1.4rem;
  text-transform: uppercase;
  font-weight: 600;
  padding: 1rem;
  text-align: center;
  border-radius: 5px;
  color: #B7B7B7;
  background: linear-gradient(145deg, #1f1f1f, #1a1a1a);
  box-shadow: 6px 6px 25px #0c0c0c, -6px -6px 25px #2e2e2e;
}
@media (min-width: 768px) {
  .listado-servicios .contenedorServicio:hover {
    cursor: pointer;
  }
}
.listado-servicios .contenedorServicio.seleccionado {
  border: none;
  outline: none;
  color: rgba(245, 245, 245, 0.3647058824);
  text-shadow: 6px 6px 25px #0c0c0c, -6px -6px 25px #2e2e2e;
  background: #1d1d1d;
  box-shadow: inset 6px 6px 25px #0c0c0c, inset -6px -6px 25px #2e2e2e;
}
.listado-servicios .contenedorServicio.seleccionado::before {
  content: "";
  width: 5px;
  height: 5px;
  border-radius: 50%;
  background: #00ff2a;
  position: absolute;
  top: 10px;
  right: 10px;
}

.paginador {
  display: flex;
  justify-content: space-between;
}
.paginador .button-pg {
  border: none;
  outline: none;
  color: #B7B7B7;
  background: linear-gradient(145deg, #1f1f1f, #1a1a1a);
  box-shadow: 6px 6px 25px #0c0c0c, -6px -6px 25px #2e2e2e;
  color: #B7B7B7;
  text-transform: uppercase;
  padding: 1.5rem;
  margin: 2rem;
  width: 20rem;
}
.paginador .button-pg:active {
  border: none;
  outline: none;
  color: rgba(245, 245, 245, 0.3647058824);
  text-shadow: 6px 6px 25px #0c0c0c, -6px -6px 25px #2e2e2e;
  background: #1d1d1d;
  box-shadow: inset 6px 6px 25px #0c0c0c, inset -6px -6px 25px #2e2e2e;
}
@media (min-width: 768px) {
  .paginador .button-pg:hover {
    cursor: pointer;
  }
}
.paginador .button-pg.hidden {
  visibility: hidden;
}

.form {
  width: 95%;
}
.form .campo {
  display: flex;
  color: #B7B7B7;
  padding: 2rem;
}
.form label {
  flex: 0 0 10rem;
  align-items: center;
  justify-content: flex-start;
  padding: 1.25rem;
  border: none;
  outline: none;
  color: #B7B7B7;
  background: linear-gradient(145deg, #1f1f1f, #1a1a1a);
  box-shadow: 6px 6px 25px #0c0c0c, -6px -6px 25px #2e2e2e;
}
.form input {
  flex: 1;
  border: none;
  outline: none;
  color: rgba(245, 245, 245, 0.3647058824);
  text-shadow: 6px 6px 25px #0c0c0c, -6px -6px 25px #2e2e2e;
  background: #1d1d1d;
  box-shadow: inset 6px 6px 25px #0c0c0c, inset -6px -6px 25px #2e2e2e;
  text-transform: uppercase;
  color: #B7B7B7;
  padding-left: 1rem;
  letter-spacing: 0.2rem;
  font-weight: 600;
  text-overflow: ellipsis;
  width: calc(100% - 2rem);
}

::-webkit-calendar-picker-indicator {
  filter: invert(1);
}

.alerta {
  margin: 5rem 0;
  padding: 1rem;
  letter-spacing: 0.5rem;
  font-weight: 600;
  color: #B7B7B7;
}
.alerta.error {
  background-color: rgba(157, 2, 7, 0.7137254902) !important;
}
.alerta.exito {
  background-color: #31572c;
}

.invalid-cite {
  text-align: center;
  margin: 5rem 0;
}

.resumen {
  border: none;
  outline: none;
  color: rgba(245, 245, 245, 0.3647058824);
  text-shadow: 6px 6px 25px #0c0c0c, -6px -6px 25px #2e2e2e;
  background: #1d1d1d;
  box-shadow: inset 6px 6px 25px #0c0c0c, inset -6px -6px 25px #2e2e2e;
  color: #B7B7B7 !important;
  padding: 2rem 0;
}
.resumen p {
  margin-bottom: 0.5rem;
}
.resumen p span {
  font-weight: 600;
}
.resumen .resumen-servicios .contenedor-servicio {
  margin-bottom: 2rem;
  padding-bottom: 2rem;
  border-bottom: 1px solid #30322b;
}
.resumen .resumen-servicios .contenedor-servicio .precio-servicio {
  font-weight: 600;
  font-size: 2.4rem;
  font-family: "Pirata One", cursive;
}
.resumen .resumen-servicios .contenedor-servicio p {
  font-weight: 600;
}
.resumen .resumen-cita {
  font-family: "Pirata One", cursive;
  margin: 4rem 0;
  letter-spacing: 1rem;
  font-size: 3rem;
}
.resumen .heading-servicios {
  font-family: "Pirata One", cursive;
  letter-spacing: 1rem;
  margin: 3rem 0;
  font-size: 2.3rem;
}

.total-pagar {
  text-align: right;
  font-weight: 600;
}
.total-pagar span {
  font-weight: 800;
  font-size: 3rem;
  font-family: "Pirata One", cursive;
}

.github {
  z-index: 999;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #1d1d1d;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 9rem;
  height: 9rem;
  color: #f5f5f5;
  border-bottom-right-radius: 50%;
}
.github i {
  font-size: 6rem;
}

.made {
  position: fixed;
  color: #f5f5f5;
  left: 2rem;
  bottom: 2rem;
}
.made a {
  color: #f5f5f5;
}
@media screen and (max-width: 768px) {
  .made {
    display: none;
  }
}